.card-container {
    display: flex;
    flex-direction: column;
    background-color: #e29339;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 20px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: greyscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}

